.city-switcher-btn {
    padding: 10px 20px;
    outline: none;
    background-color: rgb(216, 90, 90);
    border: none;
    border-radius: 5px;
    text-transform: capitalize;
    cursor: pointer;
    color: #fff;
    transition: all ease 0.5s;
    &:hover {
        box-shadow: 0 0 5px rgb(94, 19, 19);
        background-color: rgb(189, 78, 27);
    }
    &.active {
        background-color: rgb(167, 54, 54);
        transform: scale(1.08);
        box-shadow: 0 0 5px rgb(94, 19, 19);
    }
}