.header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 25px 10px;
    background-color: rgba(121, 10, 80, 0.493);
    box-sizing: border-box;
    color: #fff;
    .search-input {
        position: relative;
        input {
            outline: none;
            border: none;
            padding: 8px 16px;
            border-radius: 5px;
            font-size: 16px;
        }
        .search-result {
            position: absolute;
            background-color: rgba(26, 7, 7, 0.8);
            width: 100%;
            text-align: center;
            color: #fff;
        }
    }
}