.text {
    text-align: center;
    color: #fff;
    &-item {
        width: 1340px;
        margin: 0 auto;
        .weather-days {
            display: flex;
            justify-content: space-around;
        }
        .city-name {
            font-size: 40px;
        }
        .weather-icon {
            width: 100px;
            height: 100px;
            display: block;
            margin: 20px auto;
        }
        .more-weather {
            outline: none;
            padding: 8px 15px;
            cursor: pointer;
            margin-top: 20px;
            border: none;
            border-radius: 5px;
            background-color: rgb(97, 59, 133);
            color: #fff;
            font-size: 22px;
        }
    }
}